* {
  font-family: "Muli", sans-serif;
}

div .container {
  text-align: center;
  padding-top: 15px;
}

.row {
  display: inline-block !important;
  padding: 15px;
  margin: 15px !important;
  box-shadow: 5px 5px 10px lightgrey;
  border-radius: 15px;
}

#title {
  padding-top: 20px;
  padding-bottom: 20px;
}

.form-label {
  display: block;
  text-align: left;
}

.btn {
  margin: 5px;
  border-radius: 15px !important;
}

.btn:hover {
  color: #007bff !important;
  background-color: white !important;
}
